body {
  padding-top: 70px; }

.alert .ng-binding {
  white-space: pre-wrap; }

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important; }

#filter-panel {
  text-align: center; }
  #filter-panel .form-group {
    margin-right: 20px; }

input.ng-invalid {
  background-color: #f2dede; }

.expected-calories {
  text-align: right;
  padding-top: 20px;
  line-height: 25px;
  padding-bottom: 10px; }
  .expected-calories span {
    color: #f89406;
    font-weight: bold; }
  .expected-calories:hover .fa-pencil {
    display: inline-block;
    margin-right: 10px;
    color: #f89406;
    cursor: pointer;
    font-size: 20px; }
  .expected-calories .fa-pencil {
    display: none; }

.meals-table {
  table-layout: fixed; }
  .meals-table .col-time {
    width: 80px; }
  .meals-table .col-calories {
    width: 100px; }
  .meals-table .col-buttons {
    text-align: right;
    width: 70px; }
    .meals-table .col-buttons .fa-pencil {
      color: #f89406; }
    .meals-table .col-buttons .fa-times {
      color: #ee5f5b; }
    .meals-table .col-buttons .fa {
      cursor: pointer;
      margin-right: 10px;
      display: inline-block;
      font-size: 16px; }

.modal {
  display: block; }

.nav-username {
  margin-right: 15px !important; }

.user-change-text:hover .fa-pencil {
  display: inline-block;
  margin-left: 10px;
  color: #f89406;
  cursor: pointer; }

.user-change-text .fa-pencil {
  display: none; }
